export default {
  name: 'signup',
  components: {},
  data() {
    return {
      showPassword: false,
      user: {
        firstname: '',
        lastname: '',
        username: '',
        email: '',
        password: '',
      },
      valid: true,
      passwordRules: [
        (v) => !!v || 'ต้องการรหัสผ่าน',
        (v) => (v && v.length >= 6) || 'รหัสผ่านควรมีมากกว่า 6 ตัว',
      ],
      emailRules: [
        (v) => !!v || 'ต้องการอีเมล',
        (v) => /.+@.+\..+/.test(v) || 'อีเมลต้องมีอยู่',
      ],
    }
  },
  methods: {
    submit() {
      var state = this.$refs.form.validate()
      if (state) {
        this.$store.dispatch({
          type: 'doLogin',
          email: this.account.email,
          password: this.account.password,
        })
      }
    },
  },
}
